.container {
  padding-top: 10vh;
}
.createDrink {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 5px;
  width: 30%;
  margin: 0 auto;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  color: rgb(65, 65, 65);
}

.text {
  padding: 15px;
}

.icon {
  transform: rotate(45deg);
  margin: 10px;
}

.createDrink:hover {
  transform: scale(1.1);
}

@media screen and (min-width: 601px) {
  .icon {
    font-size: 2.5em;
  }
  .text {
    font-size: 1.5em;
  }
}

@media screen and (max-width: 600px) {
  .icon {
    font-size: 2em;
  }
  .text {
    font-size: 1em;
  }
}
